@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
@include b(load) {
  position: relative;
  height: 100%;
  background: #1b1b1b linear-gradient(180deg, #0e0b0b, #1b1b1b);

  @include e(content) {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 950px;
    height: 475px;
    margin: auto;
    text-align: center;
    background: no-repeat center;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);

    @include background-image(url(~@/views/common/img/load_game_bg.jpg));
  }

  @include e(title) {
    width: 480px;
    height: 180px;
    margin: 0 auto;
    font-size: 42px;
    line-height: 180px;
    color: #be964c;
    text-shadow: 2px 0 50px #b63131;
    letter-spacing: 2px;
    border-bottom: 1px solid #222;
  }

  @include e(text) {
    margin-top: 30px;
    font-size: 32px;
    color: #be964c;
    text-shadow: -2px 1px 40px #b63131;
    letter-spacing: 2px;
  }

  @include e(rotate) {
    position: relative;
    height: 120px;
  }
}
